#Profile {
    padding: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    > table,
    > .player-controls {
        width: 100%;
        max-width: 522px;
        margin-bottom: 15px;
    }
    > .player-controls {
        text-align: right;
    }
    td.player-data {
        width: 50%;
        > table {
            width: 100%;
            tbody td {
                text-align: right;
                padding-right: 5px;
            }
        }
    }
}
